@import "../../../assets/scss/variables";
@import "../../../assets/scss/custom";

.header {
    margin         : 0 auto;
    width          : 95%;
    display        : flex;
    align-items    : center;
    justify-content: space-between;
    padding-top    : 10px;
    position       : relative;

    @media only screen and (max-width: 1024px) {
        display    : inline-block;
        margin-left: 3%;
    }

    .headerleft {
        display    : flex;
        align-items: center;

        @media only screen and (max-width: 1024px) {
            flex-direction: column;
        }

        h1 {
            color    : $color-white;
            font-size: 24px;

            @media only screen and (max-width: 1024px) {
                font-size: 16px;
            }
        }

        ul.menu {
            padding    : 0;
            margin     : 0 30px;
            display    : flex;
            align-items: center;

            li {
                list-style-type: none;
                display        : inline;
                padding        : 0px 10px;

                a {
                    color         : $color-whitelight;
                    font-size     : 13px;
                    text-transform: capitalize;
                    display       : flex;
                    align-items   : center;

                    &:hover {
                        color: $orange;
                    }
                }
            }

            .home {
                img {
                    width       : 20px;
                    margin-right: 10px;
                }
            }

            .dashboard {
                img {
                    position: relative;
                    left    : -10px;
                }
            }
        }
    }
}

.searchbox {
    display : flex;
    width   : 250px;
    position: relative;

    @media only screen and (max-width: 1024px) {
        margin: 10px 0px 10px;
    }

    .search {
        height       : 40px;
        border-radius: 50px;
        width        : 100%;
        padding      : 0px 10px 0px 55px;
        font-size    : 14px;
        color        : #596781;

        @media only screen and (max-width: 1024px) {
            height   : 35px;
            font-size: 13px;
            padding  : 0px 10px 0px 10px;
        }
    }

    svg {
        position : absolute;
        top      : 8px;
        font-size: 28px;
        left     : 15px;
        color    : #596781;

        @media only screen and (max-width: 1024px) {
            right    : 5px;
            top      : 10px;
            font-size: 20px;
            left     : inherit;
        }
    }
}

.headerright {
    display    : flex;
    align-items: center;

    @media only screen and (max-width: 1024px) {
        justify-content: center;
    }

    svg {
        color    : $color-white;
        font-size: 24px;
        padding  : 0px 15px;
        cursor   : pointer;

        @media only screen and (max-width: 1024px) {
            padding: 0px 0px;
        }
    }

    .profilebox {
        margin-left: 15px;

        div {
            margin-right: 10px;
        }

        display    : flex;
        align-items: center;
        color      : $color-whitelight;
        font-size  : 14px;
    }

    .themeButton {
        background : $color-blueone;
        padding    : 5px 10px;
        margin-left: 25px;
        font-size  : 13px;

        @media only screen and (max-width: 1024px) {
            position: absolute;
            right   : 5px;
            top     : 15px;
        }

        &:hover {
            background: $color-blueonehover;
        }
    }
}

.mobmenu {
    position: absolute;
    top     : 15px;
    left    : 4px;
    z-index : 100;
    display : none;

    @media only screen and (max-width: 1024px) {
        display: block;
    }
}

.buttonBox {
    padding: 15px;

    button {
        margin: 0 7px;
        cursor: pointer;
    }

    a {
        background   : #fff;
        padding      : 4px 9px;
        border-radius: 3px;
        color        : #000;
        margin       : 0 7px;
    }
}

.unlockBox {
    margin-left: 50px;

    @media only screen and (max-width: 1024px) {
        margin: 10px 0px 15px;
    }

    button {
        cursor: pointer;
    }

    a {
        background   : #fff;
        padding      : 4px 9px;
        border-radius: 3px;
        color        : #000;
        font-size    : 13px;

    }
}