$color-gray: #cccccc;
$color-white: #ffffff;
$color-blue: #172b4d;
@mixin borderradius($val: 5px) {
  border-radius: $val;
}
.datepickermainbox {
  display: flex;
  align-items: center;
  position: relative;

  .dateArrow{
    padding: 10px;
    cursor: pointer;
  }

  p {
    margin: 0;
    font-size: 16px;
    margin-right: 10px;
    @media only screen and (max-width: 767px) {
      font-size: 14px;
    }
  }
  .dateinput {
    border: solid 1px $color-gray;
    height: 34px;
    padding: 0px 15px;
    cursor: pointer;
    width: 175px;
    text-align: center;
    @include borderradius(20px);
    @media only screen and (max-width: 767px) {
      height: 24px;
    }
  }
  .datepickerbox {
    position: absolute;
    top: 45px;
    left: 44px;
    z-index: 1000;
    background: $color-white;
    box-shadow: -1px 0px 10px -1px #7e7e7e;
    width: 242px;
    @include borderradius;
  }
}

.datepickermainboxSingle{
  display: inline-block;
  position: relative;
   @media (max-width:767px) {
    width:100%;
  } 
  .datepickerbox {
    position: absolute;
    top: 45px;
    left: 0px;
    z-index: 1000;
    background: $color-white;
    box-shadow: -1px 0px 10px -1px #7e7e7e;
    width: 242px;
    @include borderradius;
  }
}

.newDateInput {
  border: solid 1px #c4c4c4;
  height: 36px;
  border-radius: 5px;
  padding: 0px 5px;
  width: 230px;
  display: inline-block;
  @media (max-width:767px) {
    width:100%;
  } 
}