@import "../../assets/scss/variables";
@import "../../assets/scss/custom";

$color-bg: #f7fafc;
$color-button: #9ca4a5;

.loginbg {
    background: url(../../assets/images/loginbg.png) no-repeat;
    height: 60vh;
    position: fixed;
    bottom: 0;
    width: 100%;
    @media only screen and (max-width: 767px) {
        height: 80vh;
    }
}
.loginwrapper {
    width: 450px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @media only screen and (max-width: 767px) {
        width: 90%;
        top: 60px;
        left: 0%;
        transform: translate(0%, -0%);
        margin: 0 auto;
        right: 0;
    }
    .logo {
        display: inline-block;
        width: 100%;
        text-align: center;
        margin-bottom: 10px;
        img {
            width: 350px;
            @media only screen and (max-width: 767px) {
                width: 260px;
            }
        }
    }
    p {
        text-align: center;
        color: $color-white;
        font-size: 13px;
        margin-top: 20px;
    }
}

.loginbox {
    background: $color-bg;
    padding: 20px 35px;
    box-shadow: 1px -1px 1px 2px $color-border;
    @include border-radius;
    @media only screen and (max-width: 767px) {
        padding: 20px 20px;
    }
    p {
        text-align: center;
        margin: 30px 0px 30px;
        font-size: 14px;
        color: $fontcolor-blue;
        @media only screen and (max-width: 767px) {
            margin: 10px 0px 30px;
        }
    }
    .inputfieldbox {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        position: relative;
        svg {
            position: absolute;
            top: 10px;
            left: 15px;
            color: #445570;
        }
        .loginfield {
            width: 100%;
            height: 45px;
            @include border-radius(5px);
            background: $color-white;
            font-size: 14px;
            box-shadow: 0px 2px 3px 1px $color-border;
            border: solid 1px $color-border;
            padding: 0px 10px 0px 45px;
        }
        .eyeicon {
            position: absolute;
            right: 50px;
            top: 5px;
            svg {
                cursor: pointer;
                font-size: 20px;
            }
        }
    }
}

.buttonbox {
    display: flex;
    justify-content: center;
    margin: 50px 0px 15px;
    @media only screen and (max-width: 767px) {
        margin: 20px 0px 15px;
    }
    .createButton {
        padding: 10px 15px;
        margin-right: 15px;
        border-radius: 5px;
        font-size: 14px;
        transition: all 500ms ease-in-out;
        background: $color-button;
        color: #fff;
        text-transform: capitalize;
        border: none;
        cursor: pointer;
        
        &:hover {
            background: $color-blueonehover;
        }
    }
    .signButton {
        padding: 10px 15px;
        border-radius: 5px;
        font-size: 14px;
        transition: all 500ms ease-in-out;
        background: $color-blueone;
        color: #fff;
        text-transform: capitalize;
        border: none;
        cursor: pointer;
        margin-top: 0px;
        &:hover {
            background: $color-blueonehover;
        }
    }
}

.error {
    color: $color-red;
    margin: 5px 0 5px;
    position: relative;
    left: 5px;
    font-size: 12px;
    font-weight: 400;
    display: flex;
    height: 24px;
    align-items: center;
}