@import "../../assets/scss/variables";
@import "../../assets/scss/custom";

$color-font     : #172b4d;
$color-fontblack: #4e505d;
$total          : #f2f1e2;
$border         : #c6c6c6;
$button         :#e0e0e0;
$white          :#ffffff;


.timewrapper {
    margin    : 0 auto;
    width     : 95%;
    margin-top: 20px;

    .toptitlebox {
        display        : flex;
        justify-content: space-between;
        align-items    : center;
        margin-bottom  : 30px;

        @media only screen and (max-width: 1024px) {
            display      : inline-block;
            margin-bottom: 15px;
        }

        .leftbox {
            align-items: center;
            display    : flex;

            p {
                color    : $color-white;
                font-size: 20px;
                padding  : 0px 35px;

                @media only screen and (max-width: 1024px) {
                    font-size: 14px;
                    padding  : 0px 10px;
                    margin   : 0;
                }
            }
        }

        .rightbox {
            align-items : center;
            display     : flex;
            flex-wrap   : wrap;
            margin-right: 10px;

            p {
                color     : $color-font;
                font-size : 30px;
                width     : 100%;
                text-align: right;
                margin    : 0px 0px 7px;

                @media only screen and (max-width: 1024px) {
                    font-size : 14px;
                    padding   : 10px 10px;
                    margin    : 0;
                    text-align: left;
                }
            }

            P:last-child {
                margin   : 0;
                font-size: 14px;

                @media only screen and (max-width: 1024px) {
                    padding: 0px 10px;
                }
            }
        }
    }
}

.mainbox {
    background: $color-white;
    display   : inline-block;
    @include border-radius(10px);
    width        : 100%;
    box-shadow   : 0px 4px 6px 0px #e2e3e9;
    margin-bottom: 20px;

    .mainboxinner {
        padding: 20px 35px 20px 35px;

        @media only screen and (max-width: 1024px) {
            padding: 20px 15px 20px 15px;
        }

        h2 {
            color    : $fontcolormain;
            font-size: 16px;
        }
    }
}

.viewSelect {
    display    : flex;
    align-items: center;
    .projectTask {
        width:450px;
        margin-right:10px;
        @media (max-width:767px) {
            width:275px;
            margin-bottom: 20px;
         }
    }
    .subTask {
        width:200px;
        @media (max-width:767px) {
            width:275px;
            margin-bottom:10px;
         }
    }
    @media (max-width:767px) {
        display: block;
     }

    p {
        color       : $color-font;
        font-weight : 600;
        font-size   : 1rem;
        margin      : 0;
        margin-right: 20px;
        @media (max-width:767px) {
            margin-right: 0px;
            margin-bottom: 20px;
        }
    }
}

.generateDetail {
    margin-top   : 30px;
    margin-bottom: 30px;
    @media (max-width:767px) {
    margin-top : 0px;
    margin-bottom: 0px;
    }

    h1 {
        color    : $color-font;
        font-size: 1rem;
    }

    .detailLeft {
        display: inline-block;
        width  : 80%;

        @media only screen and (max-width:767px) {
            width        : 100%;
            margin-bottom: 15px;
        }

    }

    .detailRight {
        float: right;
        width: 20%;

        @media only screen and (max-width:767px) {
            width: 100%;
            float: left;
        }

        .reportBox {
            justify-content: flex-end;
            margin-bottom  : 10px;

            @media only screen and (max-width:767px) {
                justify-content: flex-start;
            }
        }
    }

    .reportBox {
        display      : flex;
        align-items  : center;
        width        : 100%;
        margin-bottom: 5px;

        p {
            margin             : 0;
            color              : $color-font;
            font-size          : 14px;
            margin-right       : 10px;
            -webkit-text-stroke: 1px rgba(0, 0, 0, 0.1);

            @media only screen and (max-width:767px) {
                width: 30%;
            }

            @media only screen and (max-width:1024px) {
                font-size: 12px;
            }
        }

        span {
            color    : $color-fontblack;
            font-size: 14px;

            @media only screen and (max-width:767px) {
                width: 70%;
            }

            @media only screen and (max-width:1024px) {
                font-size: 12px;
            }

        }

        .reportInput {
            width     : 35px;
            border    : solid 1px #d0d0d0;
            text-align: center;
            height    : 20px;
        }
    }
}

.progressWrapper {
    width : 80%;
    margin: 0 auto;

    @media only screen and (max-width:767px) {
        width: 100%;
    }

    ul {
        margin : 0;
        padding: 0;

        li {
            list-style-type: none;
            text-align     : left;
        }
    }
}

// Progress Header
ul.header {
    li {
        display    : inline-block;
        color      : $color-font;
        font-weight: 600;
        font-size  : 14px;
        text-align : right;

        @media only screen and (min-width:768px) and (max-width:1024px) {
            font-size: 11px;
        }

        @media only screen and (max-width:767px) {
            font-size : 12px;
            text-align: left;
        }
    }

    .first {
        width     : 30%;
        text-align: left;

        @media only screen and (max-width:767px) {
            width: 100%;
        }
    }

    .second {
        width: 30%;

        @media only screen and (max-width:767px) {
            width: 100%;
        }
    }

    .third {
        width: 20%;

        @media only screen and (max-width:767px) {
            width: 100%;
        }

    }

    .four {
        width: 15%;

        @media only screen and (max-width:767px) {
            width: 100%;
        }

    }
}

//Progress Data
ul.statusData {
    li {
        display       : inline-block;
        color         : $color-font;
        font-size     : 14px;
        padding-bottom: 5px;
        margin-bottom : 5px;
        text-align    : right;

        @media only screen and (max-width:767px) {
            text-align: left;
        }

        @media only screen and (min-width:768px) and (max-width:1024px) {
            font-size: 11px;
        }
    }

    .first {
        width        : 30%;
        border-bottom: solid 1px #172b4d;
        font-weight  : 600;
        font-size    : 13px;
        text-align   : left;

        @media only screen and (max-width:767px) {
            font-size: 11px;
            width    : 100%;
        }

        @media only screen and (min-width:768px) and (max-width:1024px) {
            font-size: 11px;
        }
    }

    .second {
        width: 30%;

        @media only screen and (max-width:767px) {
            width: 100%;
        }
    }

    .third {
        width: 10%;

        @media only screen and (max-width:767px) {
            width: 100%;
        }

        .headingBox {
            .title {
                margin: 5px 0px 15px;

            }
        }
    }

    .four {
        width: 10%;

        @media only screen and (max-width:767px) {
            width: 100%;
        }

        .headingBox {
            .title {
                margin: 5px 0px 15px;
            }
        }
    }

    .five {
        width: 15%;

        @media only screen and (max-width:767px) {
            width: 100%;
        }

        .headingBox {
            .title {
                margin: 5px 0px 15px;
            }

            .statusInput {
                width     : 50px;
                border    : solid 1px $color-font;
                text-align: center;
                height    : 20px;
            }
        }
    }
}

@media only screen and (max-width:767px) {
    ul.mobiledata {

        .second,
        .third,
        .four,
        .five {
            display: none;
        }
    }
}

//Seprator & total
.seprator {
    .first {
        text-indent: 25px;
    }
}

.totalData {
    background: $total;
    padding   : 5px 0px !important;

    li {
        margin-bottom : 0 !important;
        padding-bottom: 0 !important;
    }

    .first {
        border-bottom: solid 0px #172b4d !important;
        text-indent  : 20px;

    }

}

.progressContent {
    border-bottom : solid 2px $border;
    padding-bottom: 10px;

    h3 {
        color        : $color-font;
        font-size    : 1rem;
        margin-bottom: 15px;
        @media (max-width:767px) {
            margin-bottom: 0px;
        }
    }

    p {
        
        font-size: 14px;
        margin   : 6px 0;
    }
    .SelectTask {
        width:350px;
        margin-bottom: 15px;
        @media (max-width:767px) {
            width:100%;
        }
    }
}

.progressDescBox {
    border-bottom : solid 2px $border;
    padding-bottom: 20px;

    ul {
        h3 {
            color    : $color-font;
            font-size: 1rem;
            @media (max-width:767px) {
                margin-bottom: 5px;
            }
        }

        h4 {
            color    : $color-font;
            font-size: 14px;
            margin   : 0px 0px 5px;
        }

        li {
            font-size    : 14px;
            margin-bottom: 5px;
        }
    }

    .first {
        width  : 60%;
        display: inline-block;
        @media (max-width:767px) {
            width:100%;
        }
    }

    .second {
        width  : 20%;
        display: inline-block;
        @media (max-width:767px) {
            width:100%;
        }
    }

    .third {
        width: 20%;
        float: right;
        @media (max-width:767px) {
            width:100%;
            float: none;
        }
    }
}

.customerBox {
    ul.selectDataList {
        display: inline-block;
        width: 100%;
        li {
            display: flex;
            justify-content: space-between;
            margin-bottom: 10px;
         .selectContent {
            width:90%;
            line-height: 21px;
            @media (max-width:767px) {
                width:75%;
            }
         }  
         .selectColorBox {
            span.BackGreen {
                background: #07a44b;
            }
            span.BackPink {
                background: #e9c3c56b;
            }
            span.BackLgreen {
                background: #b9eed070;
            }
            span.BackRed {
                background: #f10400;
            }
            span {
                 width  : 25px;
                height : 15px;
                display: inline-block;
                cursor: pointer;
            }
         } 
        }
        
    }
    .first {
        width  : 85%;
        display: inline-block;
        @media (max-width:767px) {
            width:100%;
        }
    }

    .second {
        width: 15%;
        position: relative;
        float: right;
        @media (max-width:767px) {
            width:100%;
            float:none;
        }

        li {
            text-align: right;
            @media (max-width:767px) {
                text-align: left;
            }
        }
        .box2 {
            position: relative;
            top: 15px;
            
        }

        

        
    }
}

.scheduleBox {
    .first {
        width  : 7%;
        display: inline-block;
        @media (max-width:767px) {
            width:100%;
        }
    }

    .second {
        width  : 60%;
        display: inline-block;
        @media (max-width:767px) {
            width:100%;
        }
    }

    .third {
        width  : 11%;
        display: inline-block;
        float  : none;
        @media (max-width:767px) {
            width:100%;
        }
    }

    .four {
        width  : 11%;
        display: inline-block;
        @media (max-width:767px) {
            width:100%;
        }
    }

    .five {
        width  : 11%;
        display: inline-block;
        @media (max-width:767px) {
            width:100%;
        }
    }
    h3 {
        margin: 5px 0px 10px 0px;
    }

}

.commentwrapper {
    border-bottom : solid 2px $border;
    padding-bottom: 20px;

    h3 {
        color    : $color-font;
        font-size: 1rem;
        @media (max-width:767px) {
            margin-bottom: 0px;
        }
    }

}

.commentBox {
    margin-top: 10px;
    @media (max-width:767px) {
        margin-top: 0px;
    }
    textarea {
        resize   : none;
        color    : $color-font;
        font-size: 14px;
        width: 70% !important;
        margin-bottom: 15px;
        @media (max-width:767px) {
            width: 100% !important;
        }
    }
}

.generatewrapper {
    display        : flex;
    justify-content: flex-end;
}

button {
    background : $button;
    display    : flex;
    border     : none;
    padding    : 7px 20px;
    font-size  : 14px;
    font-weight: 600;
    color      : $color-font;
    cursor     : pointer;
    &:hover {
        background: $color-font;
        color     : $white;
    }

}

.loaderwrapper {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1040;
    background: rgba(0, 0, 0, 0.64);
    text-align: center;
}
.loaderinner {
    position: relative;
    height: 100vh;
    width: 100%;
}
.loaderinner img {
    position: absolute;
    top: 50%;
    left: 50%;
}
li:hover {
    background-color: transparent !important;
    cursor: auto !important;
    color: #000 !important;
}