@import "../../assets/scss/variables";
@import "../../assets/scss/custom";

.timewrapper {
    margin    : 0 auto;
    width     : 95%;
    margin-top: 20px;

    .toptitlebox {
        display        : flex;
        justify-content: space-between;
        align-items    : center;
        margin-bottom  : 30px;

        @media only screen and (max-width: 1024px) {
            display      : inline-block;
            margin-bottom: 15px;
        }

        .leftbox {
            align-items: center;
            display    : flex;

            p {
                color    : $color-white;
                font-size: 20px;
                padding  : 0px 35px;

                @media only screen and (max-width: 1024px) {
                    font-size: 14px;
                    padding  : 0px 10px;
                    margin   : 0;
                }
            }
        }

        .rightbox {
            align-items : center;
            display     : flex;
            flex-wrap   : wrap;
            margin-right: 10px;
        }
    }
}

.mainbox {
    background: $color-white;
    display   : inline-block;
    @include border-radius(10px);
    width        : 100%;
    box-shadow   : 0px 4px 6px 0px #e2e3e9;
    margin-bottom: 20px;

    .mainboxinner {
        padding: 20px 60px 0px 35px;

        @media only screen and (max-width: 1024px) {
            padding: 20px 15px 0px 15px;
        }

        h2 {
            color      : $color-black;
            font-size  : 14px;
            line-height: 16px;

            @media only screen and (min-width:1360px) and (max-width:1440px) {
                font-size: 12px;
            }
        }
    }
}

.timeentrybox {
    display        : flex;
    justify-content: space-between;
    align-items    : center;
    margin-bottom  : 20px;

    @media only screen and (max-width: 1024px) {
        display      : inline-block;
        margin-bottom: 0px;
    }
}

.Adminselect {
    display      : flex;
    align-content: center;
    margin-bottom: 25px;

    @media (max-width:767px) {
        flex-direction: column;
    }

    p {
        color      : $fontcolormain;
        font-size  : 16px;
        font-weight: 600;
        margin     : 8px 0px 0px 0px;

        @media (max-width:767px) {
            margin-top   : 0px;
            width        : auto;
            margin-bottom: 15px;
        }

        @media only screen and (min-width:768px) and (max-width:1024px) {
            margin-top  : 0px;
            display     : flex;
            align-items : center;
            margin-right: 10px;
            width       : auto;
        }

    }

    .selectReport {
        div {
            padding: 0px
        }
    }

    @media (max-width:767px) {
        .selectReport {
            width        : 100%;
            width        : 100% !important;
            margin-bottom: 15px;
        }
    }
}

.reportOddRow {
    display: flex;
    width  : 100%;

    @media(max-width:767px) {
        flex-direction: column;
    }

    .reportBox {
        background: $even;
    }
}

.reportEvenRow {
    display: flex;
    width  : 100%;

    @media(max-width:767px) {
        flex-direction: column;
    }

    .reportBox {
        background: $odd;
    }
}

.reportLastRow {
    .reportBox {
        width: 16%;
    }
}

.reportBox {
    display        : flex;
    align-items    : center;
    justify-content: center;
    text-align     : center;
    width          : 16%;
    padding        : 10px 0px;
    margin         : 0px 10px 10px 0px;
    border-radius  : 5px;
    box-shadow     : 0px 1px 2px 0px $color-borderlight;

    @media(max-width:767px) {
        width: 100%;
    }

    &:last-child {
        margin-right: 0px;
    }

    .buttonTop {
        padding: 0px 5px !important;
    }

    p {
        font-size    : 13px;
        line-height  : 17px;
        overflow     : hidden;
        text-overflow: ellipsis;
        white-space  : nowrap;
        width        : 170px;

        @media only screen and (min-width:1360px) and (max-width:1440px) {
            font-size: 11px;
        }
    }
}

.reportMessageBox {
    width        : 100%;
    display      : inline-block;
    padding      : 10px 0px;
    background   : $bluelight;
    text-align   : center;
    margin       : 0px 0px 10px 0px;
    border-radius: 5px;
    box-shadow   : 0px 1px 2px 0px $color-borderlight;

    h2 {
        text-align: left;
        padding   : 0px 8px;
    }

    p {
        text-align: left;
        font-size : 14px;
        padding   : 0px 10px;
        text-align: left;
        color     : $fontcolormain;

        @media only screen and (min-width:1360px) and (max-width:1440px) {
            font-size: 11px;
        }
    }

    .buttonTop {
        padding: 0px 5px !important;
    }
}
.label_custom {
    display: inline-block;
    width  : 150px;

    @media (max-width: 767px) {
        width        : 100%;
        margin-bottom: 5px !important;
    }
}

