@import "./variables.scss";
.inputfield::-webkit-input-placeholder {
  color: #000;
  font-size: 14px;
}
.inputfield::-moz-placeholder {
  color: #000;
  font-size: 14px;
}
.inputfield:-ms-input-placeholder {
  color: #000;
  font-size: 14px;
}

.themeButton {
  padding: 10px 15px;
  border-radius: 5px;
  font-size: 14px;
  transition: all 500ms ease-in-out;
  background: $color-bluetwo;
  color: $color-white;
  text-transform: capitalize;
  border: none;
  cursor: pointer;
  &:hover {
    background: $color-bluetwohover;
  }
}
