@import "../../assets/scss/variables";
@import "../../assets/scss/custom";

.mainwrapper {
    margin    : 0 auto;
    width     : 95%;
    margin-top: 20px;

    .toptitlebox {
        display        : flex;
        justify-content: space-between;
        align-items    : center;
        margin-bottom  : 30px;

        @media only screen and (max-width: 1024px) {
            display      : inline-block;
            margin-bottom: 15px;
        }

        .leftbox {
            align-items: center;
            display    : flex;

            p {
                color    : $color-white;
                font-size: 20px;
                padding  : 0px 35px;

                @media only screen and (max-width: 1024px) {
                    font-size: 14px;
                    padding  : 0px 10px;
                    margin   : 0;
                }
            }
        }

        .rightbox {
            align-items : center;
            display     : flex;
            flex-wrap   : wrap;
            margin-right: 10px;

            p {
                color     : $fontcolormain;
                font-size : 30px;
                width     : 100%;
                text-align: right;
                margin    : 0px 0px 7px;

                @media only screen and (max-width: 1024px) {
                    font-size : 14px;
                    padding   : 10px 10px;
                    margin    : 0;
                    text-align: left;
                }
            }

            P:last-child {
                margin   : 0;
                font-size: 14px;

                @media only screen and (max-width: 1024px) {
                    padding: 0px 10px;
                }
            }
        }
    }
}

.mainbox {
    background: $color-white;
    display   : inline-block;
    @include border-radius(10px);
    width        : 100%;
    box-shadow   : 0px 4px 6px 0px #e2e3e9;
    margin-bottom: 20px;

    .usersWrapper {
        padding: 20px 60px 0px 35px;

        @media only screen and (max-width: 1024px) {
            padding: 20px 15px 0px 15px;
        }

        h2 {
            color    : $fontcolormain;
            font-size: 16px;
        }

        button:hover  {
            color: inherit!important;
        }
    }
}

.dlgButton{
    button:hover  {
        color: inherit!important;
    }
}

.label_custom {
    display: inline-block;
    width  : 150px;

    @media (max-width: 767px) {
        width        : 100%;
        margin-bottom: 5px !important;
    }
}

.input_custom {
    width  : 230px;
    display: inline-block;

    @media (max-width: 767px) {
        width: 100%;
    }
}

.timeentrybox {
    display        : flex;
    justify-content: space-between;
    align-items    : center;
    margin-bottom  : 20px;

    @media only screen and (max-width: 1024px) {
        display      : inline-block;
        margin-bottom: 0px;
    }

    .entryleft {
        align-items: center;
        display    : flex;

    }

    .entryright {
        align-items: center;
        display    : flex;
        flex-wrap  : wrap;
    }
}

.timesentrywrapper {
    display: inline-block;
    width  : 100%;

    .headingbox {
        width         : 100%;
        margin-bottom : 15px;
        padding-bottom: 1px;
        border-bottom : solid 1px $color-borderdark;
    }

    .entrybox {
        display: inline-block;
        width  : 100%;

        .scrollbox {
            display: inline-block;
            width  : 100%;
        }
    }
}

.messagebox {
    margin: 10px 15px 20px 35px;

    @media only screen and (max-width: 1024px) {
        margin: 0px 15px 5px 15px;
    }
}

.infobox {
    display        : flex;
    justify-content: flex-start;

    .themeButton {
        padding     : 15px 23px;
        margin-right: 10px;
        display     : flex;

        @media only screen and (max-width: 1024px) {
            padding: 10px 15px;
        }
    }
}

.updatebox {
    display    : flex;
    align-items: center;

    p {
        color        : $fontcolormain;
        font-size    : 14px;
        white-space  : pre-wrap;
        padding-right: 20px;
        margin       : 0;
    }
}

.errorBorder {
    border: 2px solid red !important;
}

.Adminselect {
    display      : flex;
    align-content: center;
    margin-bottom: 8px;

    @media (max-width:767px) {
        flex-direction: column;
    }

    p {
        color      : $fontcolormain;
        font-size  : 16px;
        margin     : 0;
        display    : flex;
        align-items: center;
        font-weight: 600;

        @media (max-width:767px) {
            margin-top   : 0px;
            width        : auto;
            margin-bottom: 15px;
        }
    }

    .selectReport {
        div {
            padding: 0px
        }
    }

    @media (max-width:767px) {
        .selectReport {
            width        : 100%;
            width        : 100% !important;
            margin-bottom: 15px;
        }
    }
}

.dataBox {
    span {
        font-weight: 600;
        font-size  : 16px;
        padding    : 10px 0px 15px;
        display    : inline-block;
        color      : $fontcolormain;
        width      : 100%;

        @media (max-width: 767px) {
            padding: 0px 0px 25px;
        }
    }

    .entryDataBox {
        display: inline-block;
        width  : 100%;

        .entryLeftBox {
            display: inline-block;
            width  : 100%;
        }

        .entryRightBox {
            display: inline-block;
            width  : 400px;
        }
    }

    .CommentBox {
        width: 100%;

        textarea {
            resize       : none;
            height       : 40px;
            width        : 100%;
            padding      : 5px 10px;
            border       : solid 1px $themeborder;
            border-radius: 5px;
        }

        .inputbox {
            width: 760px;
        }
    }

    .inputbox {
        display      : inline-block;
        width        : 270px;
        margin-bottom: 10px;

        @media (max-width: 767px) {
            margin-bottom: 15px;
        }

        p {
            color        : $color-fontblack;
            font-size    : 14px;
            margin       : 0px;
            width        : 100%;
            margin-bottom: 5px;
            font-weight  : 600;
        }

        .newDateInput {
            height: 37px;
            width : 270px;
        }

        .input_custom {
            border       : solid 1px $themeborder;
            height       : 36px;
            padding      : 0px 5px;
            height       : 36px;
            border-radius: 5px;
        }

    }

}