@import "../../../assets/scss/variables";
@import "../../../assets/scss/custom";

$color-bg: #f7fafc;
$color-button: #9ca4a5;

.loginbg {
    background: url(../../../assets/images/loginbg.png) no-repeat;
    height: 60vh;
    position: fixed;
    bottom: 0;
    width: 100%;
    @media only screen and (max-width: 767px) {
        height: 90vh;
    }
}
.loginwrapper {
    width: 450px;
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    @media only screen and (max-width: 767px) {
        width: 90%;
        top: 15px;
        left: 0%;
        transform: translate(0%, -0%);
        margin: 0 auto;
        right: 0;
    }
    .logo {
        display: inline-block;
        width: 100%;
        text-align: center;
        margin-bottom: 15px;
        img {
            width: 450px;
            @media only screen and (max-width: 767px) {
                width: 260px;
            }
        }
    }
    p {
        text-align: center;
        color: $color-white;
        font-size: 13px;
        margin-top: 20px;
        a {
            color: $color-blueone;
            border-bottom: solid 1px $color-blueone;
            &:hover {
                color: $color-blueonehover;
                border-bottom: solid 1px $color-blueonehover;
            }
        }
    }
}

.loginbox {
    text-align: center;
    background: $color-bg;
    padding: 20px 35px;
    box-shadow: 1px -1px 1px 2px $color-border;
    @include border-radius;
    min-height: 300px;
    @media only screen and (max-width: 767px) {
        padding: 15px 20px;
    }
    img {
        margin-bottom: 20px;
    }
    p {
        text-align: left;
        margin: 10px 0px 5px;
        font-size: 18px;
        color: $fontcolor-blue;
        @media only screen and (max-width: 767px) {
            margin: 0px 0px 10px;
        }
    }
}
