@import "../../assets/scss/variables";
@import "../../assets/scss/custom";

$color-font: #172b4d;
$color-bgselect: #a5e1ef;
$color-fontblack: #4e505d;
$color-fontblacklight: #747580;
$color-fontgraylight: #676974;
$color-border: #a0a0a0;
$select-option: #7c7d87;
$even: #e8e8e4;
$odd: #f0f0d9;

.timewrapper {
  margin: 0 auto;
  width: 95%;
  margin-top: 20px;
  .toptitlebox {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    @media only screen and (max-width: 1024px) {
      display: inline-block;
      margin-bottom: 15px;
    }
    .leftbox {
      align-items: center;
      display: flex;
      p {
        color: $color-white;
        font-size: 20px;
        padding: 0px 35px;
        @media only screen and (max-width: 1024px) {
          font-size: 14px;
          padding: 0px 10px;
          margin: 0;
        }
      }
    }
    .rightbox {
      align-items: center;
      display: flex;
      flex-wrap: wrap;
      margin-right: 10px;
      p {
        color: $color-font;
        font-size: 30px;
        width: 100%;
        text-align: right;
        margin: 0px 0px 7px;
        @media only screen and (max-width: 1024px) {
          font-size: 14px;
          padding: 10px 10px;
          margin: 0;
          text-align: left;
        }
      }
      P:last-child {
        margin: 0;
        font-size: 14px;
        @media only screen and (max-width: 1024px) {
          padding: 0px 10px;
        }
      }
    }
  }
}
.mainbox {
  background: $color-white;
  display: inline-block;
  @include border-radius(10px);
  width: 100%;
  box-shadow: 0px 4px 6px 0px #e2e3e9;
  margin-bottom: 20px;
  .mainboxinner {
    padding: 20px 60px 0px 35px;
    @media only screen and (max-width: 1024px) {
      padding: 20px 15px 0px 15px;
    }
    h2 {
      color: $fontcolormain;
      font-size: 16px;
    }
  }
}

.label_custom {
  display: inline-block;
  width: 150px;
  margin-bottom: 5px !important;
  @media (max-width: 767px) {
    width: 100%;
  }
}
.input_custom {
  width: 345px;
  display: inline-block;
}
select.input_custom {
  width: 240px;
  @media (max-width: 767px) {
    width: 100%;
  }
}
.select_custom {
  width: 365px;
  display: inline-block;
}
.Mui-focused {
  display: none;
}

.timeentrybox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  @media only screen and (max-width: 1024px) {
    display: inline-block;
    margin-bottom: 0px;
  }
  .entryleft {
    align-items: center;
    display: flex;
    @media only screen and (max-width: 1024px) {
      display: inline-block;
    }
    p {
      color: $fontcolormain;
      font-size: 16px;
      font-weight: 600;
      padding-right: 50px;
      margin: 0;
      @media only screen and (max-width: 1024px) {
        padding-bottom: 10px;
        padding-right: 0px;
      }
    }
    .select {
      margin-right: 20px;
    }
    select {
      border: solid 2px $color-border;
      height: 37px;
      width: 120px;
      color: $select-option;
      font-size: 14px;
      @media only screen and (max-width: 1024px) {
        margin: 0px 3px;
        margin-right: 10px;
        margin-bottom: 10px;
      }
    }
  }
  .entryright {
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    @media only screen and (max-width: 1024px) {
      display: inline-block;
      margin-top: 10px;
    }
    p {
      width: 100%;
      margin: 0;
      text-align: right;
      font-weight: 600;
      font-size: 14px;
      @media only screen and (max-width: 1024px) {
        text-align: left;
      }
      &:nth-child(1) {
        color: $color-green;
      }
      &:nth-child(2) {
        color: $color-gray;
      }
    }
  }
}
.timesentrywrapper {
  display: inline-block;
  width: 100%;
  .headingbox {
    width: 100%;
    margin-bottom: 15px;
    padding-bottom: 1px;
    border-bottom: solid 1px $color-borderdark;
  }
  .entrybox {
    display: inline-block;
    width: 100%;
    .scrollbox {
      display: inline-block;
      width: 100%;
    }
  }
}
tr.totalday {
  td.firstlist {
    padding: 10px 0px;
    font-size: 16px;
    color: $color-fontgraylight;
    -webkit-text-stroke: 1px rgba(0, 0, 0, 0.2);
    @media only screen and (max-width: 1024px) {
      display: block;
    }
  }
  td.time {
    @media only screen and (max-width: 1024px) {
      display: block;
    }
    strong {
      padding: 0px 35px;
      color: $fontcolormain;
      font-size: 14px;
      color: $color-fontgraylight;
      -webkit-text-stroke: 1px rgba(0, 0, 0, 0.2);
      @media only screen and (max-width: 1024px) {
        padding: 0px 10px;
      }
    }
  }
  @media only screen and (max-width: 1024px) {
    td.secondlist {
      display: none;
    }
  }
}
.messagebox {
  margin: 0px 15px 20px 35px;
  position: relative;
  @media only screen and (max-width: 1024px) {
    margin: 0px 15px 20px 15px;
  }
}
.totalweek {
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
  padding-bottom: 10px;
  span {
    font-weight: bold;
    color: $color-fontgraylight;
    font-size: 1rem;
    padding-right: 15px;
  }
}
.infobox {
  display: flex;
  justify-content: flex-start;
  .message {
    display: flex;
    p {
      color: #9b9ca3;
      font-size: 13px;
      white-space: pre-wrap;
      padding-right: 10px;
    }
  }
  .themeButton {
    padding: 15px 23px;
    margin-right: 10px;
    display: flex;
    @media only screen and (max-width: 1024px) {
      padding: 10px 15px;
    }
  }
}
.updatebox {
  display: flex;
  align-items: center;
  p {
    color: $fontcolormain;
    font-size: 14px;
    white-space: pre-wrap;
    padding-right: 20px;
    margin: 0;
  }
}
.bottominfo {
  display: flex;
  p {
    color: $color-gray;
    font-size: 13px;
    white-space: pre-wrap;
    padding: 0px 15px;
    line-height: 18px;
  }
}
.buttonlabal {
  display: flex;
  justify-content: flex-end;
  @media only screen and (max-width: 1024px) {
    justify-content: flex-start;
  }
}
.removeButton {
  background: $color-red;
  &:hover {
    background: $color-redhover;
  }
}
table {
  border-collapse: collapse;
  margin: 0;
  padding: 0;
  width: 100%;
  display: table;
  border-spacing: 0;
  table-layout: auto !important;
}

/*Select Box CSS*/
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: solid 2px #ccc;
  background: #fff;
  background-image: none;
}
/* Remove IE arrow */
select::-ms-expand {
  display: none;
}
/* Custom Select */
.select {
  position: relative;
  display: inline-block;
  // border-radius: 0.25em;
  @media (max-width: 767px) {
    width: 100%;
  }
}
select {
  flex: 1;
  padding: 0 0.5em;
  cursor: pointer;
}
/* Arrow */
.select::after {
  content: "\25BC";
  position: absolute;
  top: 50%;
  right: 0;
  padding: 0 5px;
  transform: translate(0%, -50%);
  color: #c6baba;
  cursor: pointer;
  pointer-events: none;
  -webkit-transition: 0.25s all ease;
  -o-transition: 0.25s all ease;
  transition: 0.25s all ease;
}

.Adminselect {
  display: flex;
  align-content: center;
  margin-bottom: 8px;
  p {
    font-weight: 600;
    color: #172b4d;
    font-size: 20px;
    margin: 0;
    margin-top: 8px;
  }
}
.databox {
  span {
    font-weight: 600;
    font-size: 16px;
    padding: 10px 0px 30px;
    display: inline-block;
    color: $fontcolormain;
  }
  .inputbox {
    display: inline-block;
    width: 100%;
    margin-bottom: 20px;
    @media (max-width: 767px) {
      margin-bottom: 15px;
    }
    p {
      color: $color-fontblack;
      font-size: 14px;
      margin: 0px;
      font-weight: 600;
    }
    .input_custom {
      border: solid 1px $themeborder;
      height: 36px;
      padding: 0px 5px;
      height: 36px;
      border-radius: 5px;
    }

    .selectReport {
      height: 36px;
      display: inline-block;
      div {
        padding: 0px;
      }
    }
  }
}

.listbox {
  width: 345px;
  margin: 0;
  padding: 0;
  z-index: 1;
  position: "absolute";
  list-style: "none";
  overflow: "auto";
  max-height: 200;
  border: "1px solid rgba(0,0,0,.25)";
}
.afterClass {
  position: relative;
  input {
    cursor: pointer;
  }
}

.displayNone {
  display: none;
}

.buttonContainer {
  display: flex;
  justify-content: right;
  position: relative;
  right: 0px;
  bottom: 0px;
  width: 100%;
  background: #fff;
  box-shadow: 0px 0px 6px 3px #aba6a6;
  padding: 10px 0px;
  span {
    display: flex;
    align-items: center;
    margin-right: 15px;
  }
  button {
    background: #bb6337;
    height: 36px;
    margin-left: 15px;
  }
  button:hover {
    background: #f75404;
  }
  a {
    display: block;

    background: #7764e4;
    padding: 9px;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    border-radius: 5px;
  }
}

.cutomTableContainer {
  overflow-x: auto;
  white-space: nowrap;
  padding-bottom: 10px;
  table {
    thead {
      tr {
        border: none;
        color: inherit;
        display: table-row;
        outline: 0;
        vertical-align: middle;
      }
    }
    tr {
      th {
        font-size: 13px;
        color: #333;
        font-weight: 600;
        border-bottom: solid 1px #a0a0a0;
        padding: 10px 15px;
        width: auto;
        white-space: nowrap;
        display: table-cell;
      }
    }

    tr {
      color: inherit;
      display: table-row;
      outline: 0;
      vertical-align: middle;
      td {
        font-size: 13px;
        color: #333;
        border-bottom: solid 1px #a0a0a0;
        padding: 10px 15px;
        width: auto;
        word-break: break-word;
        display: table-cell;
      }
    }
  }
}
