@import "../../assets/scss/variables";
@import "../../assets/scss/custom";

$color-font: #b3b28b;
$color-bgselect: #a5e1ef;
$color-fontblack: #4e505d;
$color-fontblacklight: #747580;
$color-fontgraylight: #676974;
$color-border: #a0a0a0;
$select-option: #7c7d87;
$even: #e8e8e4;
$odd: #f0f0d9;

.timewrapper {
    margin: 0 auto;
    width: 95%;
    margin-top: 20px;
    .toptitlebox {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 30px;
        @media only screen and (max-width: 1024px) {
            display: inline-block;
            margin-bottom: 15px;
        }
        .leftbox {
            align-items: center;
            display: flex;
            p {
                color: $color-white;
                font-size: 20px;
                padding: 0px 35px;
                @media only screen and (max-width: 1024px) {
                    font-size: 14px;
                    padding: 0px 10px;
                    margin: 0;
                }
            }
        }
        .rightbox {
            align-items: center;
            display: flex;
            flex-wrap: wrap;
            margin-right: 10px;
            p {
                color: $color-font;
                font-size: 30px;
                width: 100%;
                text-align: right;
                margin: 0px 0px 7px;
                @media only screen and (max-width: 1024px) {
                    font-size: 14px;
                    padding: 10px 10px;
                    margin: 0;
                    text-align: left;
                }
            }
            P:last-child {
                margin: 0;
                font-size: 14px;
                @media only screen and (max-width: 1024px) {
                    padding: 0px 10px;
                }
            }
        }
    }
}
.mainbox {
    background: $color-white;
    display: inline-block;
    @include border-radius(10px);
    width: 100%;
    box-shadow: 0px 4px 6px 0px #e2e3e9;
    margin-bottom: 20px;
    .mainboxinner {
        padding: 20px 60px 0px 35px;
        @media only screen and (max-width: 1024px) {
            padding: 20px 15px 0px 15px;
        }
        h2 {
            color: $fontcolormain;
            font-size: 16px;
        }
    }
}

.label_custom {
    display: inline-block;
    width: 12%;
}
.input_custom {
    width: 345px;
    display: inline-block;
}
.select_custom {
    width: 365px;
    display: inline-block;
}
.Mui-focused {
    display: none;
}

.timeentrybox {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    @media only screen and (max-width: 1024px) {
        display: inline-block;
        margin-bottom: 0px;
    }
    .entryleft {
        align-items: center;
        display: flex;
        @media only screen and (max-width: 1024px) {
            display: inline-block;
        }
        p {
            color: $fontcolormain;
            font-size: 16px;
            font-weight: 600;
            padding-right: 50px;
            margin: 0;
            @media only screen and (max-width: 1024px) {
                padding-bottom: 10px;
                padding-right: 0px;
            }
        }
        .select {
            margin-right: 20px;
        }
        select {
            border: solid 2px $color-border;
            height: 37px;
            width: 120px;
            color: $select-option;
            font-size: 14px;
            @media only screen and (max-width: 1024px) {
                margin: 0px 3px;
                margin-right: 10px;
                margin-bottom: 10px;
            }
        }
    }
    .entryright {
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        @media only screen and (max-width: 1024px) {
            display: inline-block;
            margin-top: 10px;
        }
        p {
            width: 100%;
            margin: 0;
            text-align: right;
            font-weight: 600;
            font-size: 14px;
            @media only screen and (max-width: 1024px) {
                text-align: left;
            }
            &:nth-child(1) {
                color: $color-green;
            }
            &:nth-child(2) {
                color: $color-gray;
            }
        }
    }
}
.timesentrywrapper {
    display: inline-block;
    width: 100%;
    .headingbox {
        width: 100%;
        margin-bottom: 15px;
        padding-bottom: 1px;
        border-bottom: solid 1px $color-borderdark;
    }
    .entrybox {
        display: inline-block;
        width: 100%;
        .scrollbox {
            display: inline-block;
            width: 100%;
        }
    }
}
tr.totalday {
    td.firstlist {
        padding: 10px 0px;
        font-size: 16px;
        color: $color-fontgraylight;
        -webkit-text-stroke: 1px rgba(0, 0, 0, 0.2);
        @media only screen and (max-width: 1024px) {
            display: block;
        }
    }
    td.time {
        @media only screen and (max-width: 1024px) {
            display: block;
        }
        strong {
            padding: 0px 35px;
            color: $fontcolormain;
            font-size: 14px;
            color: $color-fontgraylight;
            -webkit-text-stroke: 1px rgba(0, 0, 0, 0.2);
            @media only screen and (max-width: 1024px) {
                padding: 0px 10px;
            }
        }
    }
    @media only screen and (max-width: 1024px) {
        td.secondlist {
            display: none;
        }
    }
}
.messagebox {
    margin: 0px 15px 5px 35px;
    position: relative;
    top: -5px;
    @media only screen and (max-width: 1024px) {
        margin: 0px 15px 5px 15px;
    }
}
.totalweek {
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
    padding-bottom: 10px;
    span {
        font-weight: bold;
        color: $color-fontgraylight;
        font-size: 1rem;
        padding-right: 15px;
    }
}
.infobox {
    display: flex;
    justify-content: flex-end;
    .message {
        display: flex;
        p {
            color: #9b9ca3;
            font-size: 13px;
            white-space: pre-wrap;
            padding-right: 10px;
        }
    }
    .themeButton {
        padding: 15px 23px;
        margin-right: 10px;
        display: flex;
        @media only screen and (max-width: 1024px) {
            padding: 10px 15px;
        }
    }
}
.updatebox {
    display: flex;
    align-items: center;
    p {
        color: $fontcolormain;
        font-size: 14px;
        white-space: pre-wrap;
        padding-right: 20px;
        margin: 0;
    }
}
.bottominfo {
    display: flex;
    p {
        color: $color-gray;
        font-size: 13px;
        white-space: pre-wrap;
        padding: 0px 15px;
        line-height: 18px;
    }
}
.buttonlabal {
    display: flex;
    justify-content: flex-end;
    @media only screen and (max-width: 1024px) {
        justify-content: flex-start;
    }
}
.removeButton {
    background: $color-red;
    &:hover {
        background: $color-redhover;
    }
}
table {
    border-collapse: collapse;
    margin: 0;
    padding: 0;
    width: 100%;
    table-layout: fixed;
}
//TH
table thead tr th.firstlist {
    width: 60%;
    padding: 5px 5px;
    @media only screen and (max-width: 1024px) {
        width: 100%;
    }
}
table thead tr th.secondlist {
    width: 20%;
    padding: 5px 4px;
    @media only screen and (max-width: 1024px) {
        width: 100%;
    }
}
table thead tr th.lastlist {
    text-align: right;

    @media only screen and (max-width: 1024px) {
        width: 100%;
    }
}
table thead tr {
    border-bottom: 2px solid $color-border;
}
table thead tr th {
    width: 8%;
    padding: 5px 3px;
    text-align: left;
    vertical-align: bottom;
    font-size: 13px;
    color: $color-fontblacklight;
    font-weight: normal;
    white-space: pre-wrap;
    @media only screen and (max-width: 1024px) {
        width: 100%;
    }
    strong {
        position: relative;
        left: -5px;
        color: $color-fontblack;
    }
}
//TD
table tbody tr td.firstlist {
    width: 60%;
    @media only screen and (max-width: 1024px) {
        width: 100%;
    }
    select {
        border: solid 2px $color-border;
        height: 30px;
        width: 100%;
        font-size: 13px;
        color: $select-option;
        -webkit-text-stroke: 1px rgba(0, 0, 0, 0.2);
        white-space: nowrap;
        text-overflow: ellipsis;
        padding-right: 20px;
    }
}
table tbody tr td.secondlist {
    width: 20%;
    @media only screen and (max-width: 1024px) {
        width: 100%;
    }
    select {
        border: solid 2px $color-border;
        height: 30px;
        width: 100%;
        font-size: 13px;
        color: $select-option;
        -webkit-text-stroke: 1px rgba(0, 0, 0, 0.2);
        white-space: nowrap;
        text-overflow: ellipsis;
        padding-right: 20px;
    }
}
table tbody tr td.lastlist {
    text-align: right;

    @media only screen and (max-width: 1024px) {
        width: 100%;
    }
    .themeButton {
        padding: 5px 5px;
        margin-right: 10px;
        display: flex;
        @media only screen and (max-width: 1024px) {
            position: inherit;
            top: 0px;
            right: 0px;
            margin-left: 10px;
        }
        svg {
            font-size: 18px;
        }
    }
}
table tbody tr td {
    width: 8%;
    padding: 10px 15px;
    position: relative;
    width: 100%;
    @media only screen and (max-width: 1024px) {
        width: 100%;
        padding: 10px 0px;
    }
    .entryinput {
        height: 25px;
        width: 90%;
        padding: 0px 10px;
        font-size: 13px;
        border: solid 2px $color-border;
        @media only screen and (max-width: 1024px) {
            margin-left: 10px;
            width: 90px;
        }
    }
    @media only screen and (max-width: 1024px) {
        .select {
            display: inline-block;
            width: 90%;
            margin-left: 10px;
        }
    }
}
table tbody tr.bgboxselect td {
    background: $color-bgselect;
}
table tbody tr.evan {
    background-color: $even;
}
table tbody tr.odd {
    background-color: $odd;
}
//Gap
tr.gapdata {
    td {
        background: $color-white;
        padding: 3px 0px;
        width: 100%;
    }
}
tr.gapgray {
    td {
        background: $color-border;
        padding: 1px 0px;
        width: 100%;
    }
}
@media screen and (max-width: 1024px) {
    table {
        border: 0;
    }
    table caption {
        font-size: 1.3em;
    }
    table thead {
        border: none;
        clip: rect(0 0 0 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        position: absolute;
        width: 1px;
    }
    table td {
        border-bottom: solid 2px #dadada;
        display: block;
        font-size: 0.8em;
        text-align: left;
    }
    table td:before {
        content: attr(data-label);
        float: left;
        font-weight: bold;
        text-transform: uppercase;
        width: 100%;
        padding-left: 10px;
    }
    table td:last-child {
        border-bottom: 0;
    }
}
/*Select Box CSS*/
select {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    outline: 0;
    box-shadow: none;
    border: solid 2px #ccc;
    background: #fff;
    background-image: none;
}
/* Remove IE arrow */
select::-ms-expand {
    display: none;
}
/* Custom Select */
.select {
    position: relative;
    display: flex;
    overflow: hidden;
    border-radius: 0.25em;
}
select {
    flex: 1;
    padding: 0 0.5em;
    cursor: pointer;
}
/* Arrow */
.select::after {
    content: "\25BC";
    position: absolute;
    top: 50%;
    right: 0;
    padding: 0 5px;
    transform: translate(0%, -50%);
    color: #c6baba;
    cursor: pointer;
    pointer-events: none;
    -webkit-transition: 0.25s all ease;
    -o-transition: 0.25s all ease;
    transition: 0.25s all ease;
}
/* Transition */
.select:hover::after {
    color: #b2a5a5;
}
.errorBorder {
    border: 2px solid red !important;
}
